// View applied styles by going to https://your-site.com/wp-content/themes/videojet/assets/style-guide/elements.html

html {
	@include border-box;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

/* stylelint-disable */
*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;

	-webkit-tap-highlight-color: rgba(0,0,0,0);
}
/* stylelint-enable */

/**
 * 1. Fallback for when there is no custom background color defined. [WordPress]
 * 2. Remove default margin. [normalize.css]
 * 3. Set the base elements `font-size` to the value of your choosing.
 * 4. Work out the unitless `line-height` for your project based around your
 *    desired `line-height` (defined previously in pixels), and your projects
 *    base font size.
 */
body {
	@include font-family();
	@include body-text( body );

	background: $color__background-body;
	color: $color__body-text;

}



/*------------------------------------*\
    #LINKS
\*------------------------------------*/
a {
	cursor: pointer;
	color: $color__link;
	text-decoration: none;
	transition: 0.3s;

	&:focus {
		outline: 5px auto -webkit-focus-ring-color;
		outline-offset: -2px;
	}

	&:hover,
	a:active {
		color: $color__link-hover;
		text-decoration: underline;
		outline: 0;
	}
}

/*------------------------------------*\
    #SUB & SUP
\*------------------------------------*/

sub,
sup {
	position: relative;
	font-size: 75%;
	line-height: 0;
	vertical-align: baseline;
}
sup { top: -0.5em; }
sub { bottom: -0.25em; }

/*------------------------------------*\
    #HEADINGS
\*------------------------------------*/
/* stylelint-disable */
h1 {
	@include body-text( h1 )
}

h2 {
	@include body-text( h2 )
}

h3 {
	@include body-text( h3 )
}

h4 {
	@include body-text( h4 )
}

h5 {
	@include body-text( h5 )
}

h6 {
	@include body-text( h6 )
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 10px 0;
	text-rendering: optimizelegibility;
}

/*---------------------------
		Lists
---------------------------*/

ul,
ol {
	margin: 0 0 18px 0;

	&.no-bullet {
		&,
		ul,
		ol {
			margin: 0;
			padding: 0;
			list-style: none;
		}
	}
}

ul {
	padding: 0;
	margin: 0 0 10px 25px;

	@include mq( md ) {
		margin: 0 0 5px 0;
	}
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 18px;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 24px 24px;
}

p {
	&:empty {
		display: none;
	}
}

li > ul,
li > ol {
	margin-bottom: 0;
}

/*------------------------------------*\
    #TABLE
\*------------------------------------*/

table {
	max-width: 100%;
	background-color: transparent;
	border-collapse: collapse;
	border-spacing: 0;

	@include mq( md ) {
		max-width: 350px;
	}
}

/* Button
--------------------------------------------- */

.wp-block-button {

	.wp-block-button__link {
		border-radius: 0;
		font-weight: 700;
		font-size: 16px;
		line-height: 18px;
		padding: 20px 24px;

		&:hover {
			background: #dddddd;
			text-decoration: none;
		}

	}
}
/* stylelint-enable */

/*------------------------------------*\
    Form Fields
\*------------------------------------*/

input,
button,
select,
textarea {
	font-family: $font__main;
}

select,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
	display: inline-block;
	height: 20px;
	width: 206px;
	padding: 14px 6px; /* adjusting border-box difference on live site */
	margin: 0 0 10px;
	line-height: 1.43;
	color: #333333;
	vertical-align: middle;
	border-radius: 4px;
	background-color: $white;
	border: 1px solid #dddddd;
	box-shadow: inset 0 1px 1px 0.75;
	transition: border linear 0.2s, box-shadow linear 0.2s;
}

textarea {
	height: auto;
	padding: 4px 6px;
	max-width: 100%;
	outline: none;
	overflow: auto; // Remove the default vertical scrollbar in IE 10+.
}

select {
	box-shadow: none;
	transition: none;
	width: 220px;
	padding: 0 6px;
}

select,
input[type="file"] {
	height: 30px;
	line-height: 2.15;
}

button {
	margin: 0;
}

label {
	display: block;
	margin-bottom: 5px;
}

label,
input,
button,
select,
textarea {
	@include body-text( body );
	font-weight: 400;
}

label,
select,
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
input[type="radio"],
input[type="checkbox"] {
	cursor: pointer;
}

input[type="search"] {
	-webkit-appearance: textfield;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button {
	-webkit-appearance: none;
}

/*------------------------------------*\
    #OTHER
\*------------------------------------*/

